@import '../styles/functions';
.drum-machine {
    &__display {
        position: relative;
        z-index: 10;
        background: var(--primary-color);
        width: 100%;
        height: calc(100% / 3);
        height: 25%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding: 20px 0;
        align-items: flex-end;
        color: var(--white);
        box-shadow: 0 3px 5px var(--gray-20);
    }
}
